.BlogPage_main {
  background-color: white;
  display: flex;
  flex-direction: row;

  margin-top: 2vw;  
}
.BlogPage-date {
  display: flex;
  flex-direction: column;
  margin-top: 90px;
  margin-left: 4.375vw;

  font-family: var(--font-inter);
  font-style: normal;
  font-weight: 300;
  font-size: 0.677vw;
  line-height: 0.833vw;

  color: rgba(52, 52, 52, 0.8);
}
.BlogPage-text {
  display: flex;
  flex-direction: column;
  margin-top: 90px;
  width: 1061px;
}
.blog_name p {
  width: 100%;
  margin-top: 3.49vw;

  font-family: var(--font-prata);
  font-style: normal;
  font-weight: 400;
  font-size: 3.49vw;
  line-height: 4.74vw;

  color: #343434;
}
.blog_describe {
  display: flex;
  align-items: left;
  justify-content: left;
  margin-top: 3.646vw;
}
.blog_describe p {
  width: 70%;
  font-family: var(--font-inter);
  font-style: italic;
  font-weight: 300;
  font-size: 1.042vw;
  line-height: 1.25vw;

  color: #343434;
}
.blog_describe_label {
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: end;
}
.blog_describe_label p {
  font-family: var(--font-inter);
  font-style: italic;
  font-weight: 300;
  font-size: 0.99vw;
  line-height: 1.198vw;
  /* identical to box height */

  color: rgba(52, 52, 52, 0.5);
}
.border_line {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1.667vw;

  width: 100%;
}
.date_label {
  margin-top: 6.115vw;
  width: 3.542vw;
}
.arrow1,
.arrow2 {
  margin-left: 2.987vw;
  margin-right: 1.094vw;
}
.arrow2 {
  margin-top: 1.032vw;
}
.date_read {
  width: 3.542vw;
}
.blog_picture {
  height: 21.823vw;
  object-fit: cover;
}
.blog_picture img {
  width: 100%;
  backdrop-filter: blur(11px);
  border-radius: 1.146vw 1.146vw 0px 0px;
}
.blog_picture:hover {
  filter: blur(0.156vw);
}
.BlogPage-other {
  margin-left: 4.375vw;
  border-left: 2px dashed #e7e7e7;
  opacity: 0.6;
}
.other2 {
  margin-left: 2.031vw;
  width: 21.406vw;
}
.BlogPage-search {
  margin-top: 2.604vw;
  display: flex;
  width: 21.406vw;
  height: 2.604vw;

  box-shadow: 0px 16px 40px rgba(112, 144, 176, 0.2);
  border-style: none;
  border-radius: 18px;

  font-family: var(--font-inter);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */

  color: rgba(52, 52, 52, 0.33);
  align-items: center;
  justify-content: center;
}
.BlogPage-Hashtags {
  display: flex;
  width: 73%;

  overflow-x: hidden;
}
.BlogPage-search_input {
  border-style: none;
  width: 80%;
  height: 60%;
  margin-right: 0.781vw;
  outline: none;
}
.other_tag_links {
  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 100%;

  margin-top: 1.406vw;
}

.link {
  display: flex;
}

.facebook_link,
.twitter_link,
.instagram_link {
  width: 0.833vw;
  height: 0.677vw;
}
.twitter_link,
.instagram_link {
  margin-left: 1.198vw;
}
.profile {
  display: flex;
  flex-direction: column;
  align-items: baseline;
}
.logo_name {
  display: flex;
  flex-direction: row;
}
.profile_logo {
  width: 2.552vw;
  height: 2.552vw;
  border-radius: 1.302vw;

  margin-top: 1.406vw;
}
.profile_name {
  margin-top: 2.135vw;
  margin-left: 0.9vw;

  font-family: var(--font-inter);
  font-style: normal;
  font-weight: 300;
  font-size: 1.042vw;
  line-height: 1.25vw;

  color: #343434;
}
.subscriber_count {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 0.677vw;

  font-family: var(--font-inter);
  font-style: normal;
  font-weight: 400;
  font-size: 1.042vw;
  line-height: 0.938vw;

  color: rgba(52, 52, 52, 0.6);
}
.profile_describe {
  width: 15.052vw;
  margin-top: 0.677vw;

  font-family: var(--font-inter);
  font-style: normal;
  font-weight: 300;
  font-size: 0.781vw;
  line-height: 0.938vw;

  color: rgba(52, 52, 52, 0.6);
}
.profile_subscribe {
  all: unset;
  cursor: pointer;
  margin-top: 0.923vw;
  padding: 7px 41px;
  background: #343434;
  box-shadow: 0px 0.833vw 2.083vw rgba(112, 144, 176, 0.2);
  border-radius: 8px;

  font-family: var(--font-inter);
  font-style: normal;
  font-weight: 300;
  font-size: 0.729vw;
  line-height: 0.885vw;
  /* identical to box height */

  color: #ffffff;
}
.recomendation {
  margin-top: 0.923vw;
}
.rec_first {
  font-family: var(--font-inter);
  font-style: normal;
  font-weight: 400;
  font-size: 0.938vw;
  line-height: 1.146vw;
  /* identical to box height */

  color: #343434;
}
.rec_second {
  margin-top: 0.156vw;

  font-family: var(--font-inter);
  font-style: normal;
  font-weight: 300;
  font-size: 0.573vw;
  line-height: 0.677vw;

  color: rgba(52, 52, 52, 0.7);
}

.BlogPage-other:hover {
  opacity: 1;
}
.link ul li{
    display: inline;
    margin-right: 16px;
    padding: 4px;
}

.link ul li:last-child{
    display: inline;
    margin-right: 0px;
}
.facebook_link{
    display: flex;
    cursor: pointer;
    fill: url('#paint0_linear_1041_299');
}

.facebook_link:hover ~ defs stop:first-child{
    stop-color: var(--color-facebookgradone);
}

.facebook_link:hover ~ defs stop:last-child{
    stop-color: var(--color-facebookgradtwo);
}

.twitter_link{
    display: flex;
    cursor: pointer;
    fill: url('#paint0_linear_921_130');
}

.twitter_link:hover ~ defs stop:first-child{
    stop-color: var(--color-twittergradone);
}

.twitter_link:hover ~ defs stop:last-child{
    stop-color: var(--color-twittergradtwo);
}

.instagram_link{
    display: flex;
    cursor: pointer;
    fill: url('#paint0_linear_933_132');
}

.instagram_link:hover ~ defs stop:first-child{
    stop-color: var(--color-instgradone);
}

.instagram_link:hover ~ defs stop{
    stop-color: var(--color-instgradtwo);
}

.instagram_link:hover ~ defs stop:last-child{
    stop-color: var(--color-instgradthree);
}
.other-goBack p{
    font-family: 'Inter';
font-style: normal;
font-weight: 400;
font-size: 20px;
line-height: 24px;

color: #343434;
}
.other-goBack{
    width: 20%;
    margin-top: 35px;
    cursor: pointer;
}
.goBack-Line{
    margin-top: 13px;
}
.blog_describe_line{
    margin-top: 5.156vw;
}